
.neirong1 {
		width: 1000px;
		height: 1000px;
		background: #F4F4F4;
		/*border: 1px solid red;*/
		margin: 0 auto;
    overflow-y:scroll
}
.limit{
    width: 800px;
    height: 800px;
    /*border: 1px solid red;*/
    margin: 50px auto;
    clear: both;
}
.limit li {
    width: 240px;
    height: 140px;
    /*border: 1px solid red;*/
    float: left;
    margin: 10px 14px 10px 10px;
    font-size: 20px;
    padding-top: 100px;
    text-align: center;
    color: white;
    background: #63c8e9;
}
.limit li:hover{
    color: black;
    background: white;
    -webkit-transition: 1s;
    transition: 1s;
}
.neirong1 h1 {
		text-align: center;
		padding: 30px;
    /*border: 1px solid red;*/
    background: #cbecf7;
    color: #828a8b;
}

